
import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ElementCombinationSearch } from './element-combination-search/element-combination-search.component';
import { ElementCombinationAdd } from './element-combination-add/element-combination-add.component';





const routes: Routes = [
    {
        path: '',
        component: ElementCombinationSearch,
    },
    { path: 'element-combination-add', component: ElementCombinationAdd, }
];
export const ElementCombinationRouting: ModuleWithProviders = RouterModule.forChild(routes);
